.grid {
   margin: 15px 0 0;
}

.NavbarItems {
   background: linear-gradient(
      90deg,
      rgb(110, 94, 254) 0%,
      rgba(73, 63, 252, 1) 100%
   );
   height: 80px;
   display: flex;
   align-items: center;
   font-size: 1.2rem;
}

.navbar-logo {
   color: white;
   justify-self: start;
   margin-left: 20px;
   margin-right: 5px;
   cursor: pointer;
}

.fa-react {
   margin-left: 0.5rem;
   font-size: 1.6rem;
}

.nav-item {
   margin: 5px;
   cursor: pointer;
   font-family: system-ui;
   color: white;
}
.nav-button {
   margin: 5px;
   border-radius: 5px;
   background: transparent;
   cursor: pointer;
   font-size: 20px;
   font-weight: 100;
   border: none;
   color: white;
   padding-top: 10px;
   padding-bottom: 10px;
   padding-right: 5px;
   padding-left: 5px;
   text-decoration: none;
}
.nav-button:hover {
   background-image: linear-gradient(#2059f5, #0d88ec);
   background-color: 2035f5;
}
.nav-button:active {
   filter: brightness(85%);
   transform: scale(0.98);
}

.nav-button:focus {
   outline: 0;
}
